export default {
    props: {
        notification: {
            type: Object,
            default: () => {},
        },
    },
    methods: {
        formatTime(time) {
            const date = new Date(time);
            return `${date.getDate().toString().padStart(2, '0')}-${(date.getMonth() + 1).toString().padStart(2, '0')}-${date.getFullYear()} ${date.getHours().toString().padStart(2, '0')}:${date.getMinutes().toString().padStart(2, '0')}`;
        },
        customJs() {
            var parentElement = document.getElementById(`notification-${this.notification.id}`);
            var pTags = parentElement.getElementsByTagName('p');
            for (var i = 0; i < pTags.length; i++) {
                var imgTag = pTags[i].getElementsByTagName('img')[0];
                if (imgTag) {
                    pTags[i].style.textAlign = "center";
                }
            }
        }
    },
    mounted() {
        if (this.notification) {
            this.customJs()
        }
        
    },
}
